.tab-box .tab-header {
  margin: 0;
  padding: 0;
}
.tab-box .tab-header > li {
  display: inline-block;
  width: 20%;
  line-height: 30px;
  text-align: center;
  box-sizing: border-box;
}
.tab-box .tab-header > li.active {
  border-bottom: 3px solid #767676;
}
.tab-box .tab-body > div {
  display: none;
}
.tab-box .tab-body > div.active {
  display: block;
}
.tab-box .tab-body > div .editor-box {
  width: 100%;
  height: 300px;
  overflow-y: auto;
  overflow-x: hidden;
}
.tab-box .tab-body > div .row {
  height: 300px;
}
.tab-box .tab-body > div .row .col-5 {
  height: 300px;
  width: 50%;
  float: left;
  border: 1px dotted #767676;
  box-sizing: border-box;
}

.list-box {
  margin: 0;
  padding: 0;
}
.list-box > li {
  width: 150px;
  line-height: 30px;
  display: inline-block;
  overflow: hidden;
  box-sizing: border-box;
}
.list-box > li img {
  max-width: 100%;
  max-height: 120px;
}
.list-box > li.selected {
  border: 1px solid #767676;
}